import rule from '@/utils/validate-rule'
export const contactRule={
    linkman_name: [
      rule.required({
        message: "请输入姓名"
      }),
      rule.max({
        max: 20,
        message: "最多不能超过20个字"
      }),
      rule.min({
        min: 2,
        message: "至少2个字"
      })
    ],
    linkman_contact: [
      {
        validator: rule.phone
      }
    ],
    linkman_post: [
      rule.required({
        message: "请输入联系人职务"
      })
    ]
  }
export const contactData={
    linkman_name: "",
    linkman_contact: "",
    linkman_post: ""
  }


